.button {
  background: #ef4a24;
  color: white;
  font-size: 18px;
  border-radius: 16px;
  border: 1px solid rgb(233, 158, 114);
  padding: 8px 20px;
  display: inline-block;
  transition: 0.2s background;
  cursor: pointer;
}
.button:hover {
  background: #ef6e24;
}
.button:disabled,
.button[disabled] {
  background-color: #cccccc;
  color: #666666;
  border: none;
  cursor: no-drop;
}
