.container {
  margin: 0 auto;
}
@media (min-width: 576px) {
  .container {
    max-width: 540px;
  }
}
@media (min-width: 768px) {
  .container {
    max-width: 720px;
  }
}
@media (min-width: 992px) {
  .container {
    max-width: 960px;
  }
}
@media (min-width: 1200px) {
  .container {
    max-width: 1140px;
  }
}
@media (min-width: 1400px) {
  .container {
    max-width: 1220px;
  }
}
@media (min-width: 1500px) {
  .container {
    max-width: 1320px;
  }
}
@media (max-width: 576px) {
  .container {
    max-width:90%
  }
}