.item {
  box-shadow: 2px 4px 10px 0px rgb(34 60 80 / 10%);
  background: white;
}
.content {
  padding: 20px;
}
.img {
  width: 100%;
}
.title {
  font-size: 18px;
  font-weight: 600;
  color: #000;
}
.text {
  margin-top: 10px;
  color: rgb(0, 0, 0);
  line-height: 1.5;
}
.targetWrapper {
  margin-top: 20px;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}
.price {
  font-size: 20px;
  font-weight: 600;
}